.community-list {
  width: 100%;
  padding: 2rem 0.3rem 0 0.3rem;
  background: #fff;
  box-sizing: border-box;
  padding-bottom: 0.7rem;
}
.community-list ul {
  overflow: hidden;
}
.community-list li {
  width: 33.33%;
  padding-top: 0.6rem;
  box-sizing: border-box;
  float: left;
  cursor: pointer;
  border-bottom: 1px solid #f4f4f4;
  border-right: 1px solid #f4f4f4;
}
.community-list li:nth-child(3n) {
  border-right: none;
}
.community-list li img {
  height: 1rem;
  margin: 0 auto;
}
.community-list li p {
  text-align: center;
  margin: 0.3rem 0 0.5rem;
  font-size: 0.28rem;
  width: 100%;
  color: #969696;
}



