
.index_top {
	width: 100%;
	height: 5.6rem; 
	background: url("../../assets/images/index_bg.jpg")center no-repeat;
	background-size: cover;
	position: relative;
}
.logo {
	display: block;
	width: 2.4rem;
	height: 3rem;
	background: url("../../assets/images/logo@2x.png")center no-repeat;
	background-color: rgba(255, 255, 255, .7);
	background-size: 2.4rem;
	margin: 0 auto;
}
.user_name {
	font-size: .6rem;
	color: #f2c466;
	font-weight: normal;
	padding-left: .55rem;
}
.weather_date {
	padding: 0 .55rem;
	color: #fff;
	overflow: hidden;
	margin-top: 0.2rem;
}
.weather_date>li.date {
	float: left;
	font-size: .28rem;
	width: 30%;
}
.weather_date>li.weather {
	float: right;
	font-size: .28rem;
	width: 70%;
}
.weather_date>li.date .left {
	float: left;
	width: .6rem;
	height: .6rem;
	background: url("../../assets/images/icon_date.png")center no-repeat;
	background-size: .6rem;
	margin-right: .12rem;
}
.weather_date>li.weather .left {
	float: right;
}
.weather_date>li.weather .right {
	float: right;
	width: .7rem;
	height: .6rem;
	background-size: .7rem;
	margin-left: .12rem;
}
.weather_date>li.weather .right img {
	width: 100%;
}
.smart_home {
	text-align: center;
	position: absolute;
	width: 100%;
	left: 0;
	bottom: -.45rem;
}
.smart_home button {
	width: 6.55rem;
	height: .9rem;
	background: url("../../assets/images/btn_bg.png")center no-repeat;
	background-size: cover;
	border: none;
	font-size: .36rem;
	color: #fff;
}
.index_content {
	padding: 0rem .3rem;
	background: #fff;
	padding-top: .7rem;
}
.index_content img {
	height: .5rem;
	margin: .1rem auto;
}
.index_content .swiper-slide {
	text-align: center;
}
.index_content .swiper-container {
	padding-bottom: .3rem;
	border-bottom: 1px solid #d4d4d4;
}
.map_list {
	background: #fff;
	padding: .3rem;
}
.index {
	box-shadow: 0px 0px 6px 2px rgba(0, 0, 0, .3);
	width: 100%;
	transition: left .5s;
	left: 0;
	overflow-x: hidden;
}
.index_tab {
	position: absolute!important;
	top: 20px;
	left: 0;
	width: 100%;
	background: none!important;
	z-index: 20;
}
.index .m-navbar:after {
	border-bottom: none;
}
.index .user {
	display: inline-block;
	width: 100%;
	height: .5rem;
	background: url('../../assets/images/icon_user.png')left center no-repeat;
	background-size: .38rem;
}
.index .menu {
	display: inline-block;
	width: 100%;
	height: .5rem;
	background: url('../../assets/images/icon_menu.png')right center no-repeat;
	background-size: .38rem;
}
.map_list {
	padding: .3rem;
}
.map_list img {
	width: 100%;
	height: 2.5rem;
}
.map_list li:first-child {
	border-bottom: 1px solid #eee;
}
.pic_show {
	position: relative;
}
.pic_show h4 {
	position: absolute;
	left: .2rem;
	top: .2rem;
	font-size: .36rem;
	color: #fff;
	width: 80%;
	font-weight: normal;
}
.address {
	overflow: hidden;
	margin-top: .34rem;
	margin-bottom: .34rem;
}
.address .left {
	float: left;
	width: 75%;
	font-size: .28rem;
	min-height: .5rem;
}
.address .right {
	float: left;
	width: 25%;
	text-align: right;
}
.address .right button {
	width: 95%;
	height: .64rem;
	background: #f0c366;
	color: #fff;
	font-size: .28rem;
	border-radius: .64rem;
	border: none;
}
.mark {
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	z-index: 400;
	background: rgba(0, 0, 0, 0.1);
	transition: left .5s;
	opacity: 0;
}
footer {
	transition: left .5s;
}
.smart_home i {
	display: inline-block;
	width: .3rem;
	height: .4rem;
	background: url("../../assets/images/icon-smart.png")center no-repeat;
	background-size: .28rem;
	vertical-align: middle
}
.ap_detail {
	font-size: .3rem;
	color: #666;
	font-weight: normal;
	padding: .2rem 0;
}
.appartment .appartment_top {
	width: 100%;
	height: 4.7rem;
	background: url('../../assets/images/index_bg.jpg')center no-repeat;
	background-size: cover;
}
.app_content {
	padding: .3rem;
}
.app_content h4 {
	font-size: .4rem;
	color: #494949;
}
.map_content {
	width: 100%;
	height: 2.9rem;
	margin-top: .5rem;
	background: #fff;
}
.map_content img {
	width: 100%;
	height: 100%;
}
.app_content .add {
	overflow: hidden;
	font-size: .28rem;
	padding: .2rem 0;
	border-bottom: 1px solid #eee;
}
.app_content .add li {
	min-height: .5rem;
}
.app_content .add li:last-child {
	text-align: right;
	font-size: .36rem;
	color: #f0c366;
}
.apart_profile {
	padding: .5rem 0;
	border-bottom: 1px solid #eee;
	font-size: .28rem;
}
.apart_profile h4 {
	font-size: .32rem;
	color: #2b2b2b;
	font-weight: normal;
}
.equipment {
	padding: .55rem 0;
	border-bottom: 1px solid #eee;
}
.equipment img {
	width: .55rem;
	margin: 0 auto;
}
.equipment .swiper-slide {
	text-align: center;
}
.virtual {
	padding: .35rem 0;
	border-bottom: 1px solid #eee;
}
.virtual h4 {
	font-size: .32rem;
	font-weight: normal;
	color: #f0c366;
	padding-bottom: .4rem;
}
.virtual h4 i {
	float: left;
	width: .5rem;
	height: .32rem;
	background: url("../../assets/images/icon_720.png")center no-repeat;
	background-size: .5rem;
	margin-top: 3px;
	margin-right: 5px;
}
.virtual img {
	width: 2.1rem;
	height: 1.65rem;
}
.apartment {
	padding-bottom: .55rem;
	border-bottom: 1px solid #eee;
}
.apartment h4,
.fire h4,
.how h4 {
	font-size: .32rem;
	font-weight: normal;
	padding: .4rem 0;
}
.apartment img {
	width: 100%;
	height: 4.36rem;
}
.apartment h5 {
	font-size: .3rem;
	color: #464646;
	padding: .2rem 0;
}
.apart_style {
	overflow: hidden;
	font-size: .28rem;
	color: #474747;
}
.apart_style i {
	float: left;
	width: .4rem;
	height: .26rem;
	margin-top: .1rem;
	margin-right: .1rem;
}
.apart_style li:first-child i {
	background: url("../../assets/images/icon_number@2x.png")center no-repeat;
	background-size: .5rem;
}
.apart_style li:last-child i {
	background: url("../../assets/images/icon_person@2x.png")center no-repeat;
	background-size: .5rem;
}
.apart_util {
	color: #474747;
	margin-top: .40rem;
	font-size: .28rem;
	overflow: hidden;
}
.apart_util>li>div:last-child {
	text-align: right;
	color: #2b2b2b;
	font-weight: 600;
}
.apart_btn {
	overflow: hidden;
	border: 1px solid #f0c366;
	margin-top: .5rem;
}
.apart_btn li {
	height: .74rem;
	line-height: .74rem;
	text-align: center;
	font-size: .32rem;
	color: #f0c366;
}
.apart_btn li {
	background: #f0c366;
	color: #fff;
}


/*.apart_btn li:last-child{background: #fff;}*/
.fire {
	padding: .55rem 0;
	border-bottom: 1px solid #eee;
}
.fire img {
	width: 5.5rem;
	height: 3.7rem;
}
.how p,
.hwo>div {
	font-size: .28rem;
	color: #4c4c4c;
}
.how_txt {
	margin-top: .5rem;
}
.how_txt ul li:first-child {
	color: #404040;
	font-size: .28rem;
}
.how_txt ul li:last-child {
	text-align: right;
	font-size: .28rem;
}
.how_txt p {
	color: #444;
}
.how_txt ul li:last-child i {
	display: inline-block;
	width: .3rem;
	height: .3rem;
	vertical-align: middle;
	margin-right: 5px;
}
.how_phone i {
	background: url("../../assets/images/icon_phone@2x.png")center no-repeat;
	background-size: .28rem;
}
.how_location i {
	background: url("../../assets/images/icon_locale@2x.png")center no-repeat;
	background-size: .28rem;
}
.appartment header {
	background: none!important;
}
.appartment .m-navbar:after {
	border-bottom: none;
}
.appartment .export {
	display: block;
	width: .4rem;
	height: .4rem;
	background: url("../../assets/images/icon_export.png")center no-repeat;
	background-size: .35rem;
}
.tabbar-fixed {
	z-index: 380!important;
}
.how .cell-icon {
	width: .6rem;
}



